'use client';

import AnalyticsChart from '@/src/component/dashboard/AnalyticsChart';
import Search from '@/src/component/dashboard/Search';
import TopSection from '@/src/component/dashboard/TopSection';
import UserTable from '@/src/component/dashboard/UserTable';
import useAuthGuard from '@/utils/withAuth';
import React, { useState, useEffect } from 'react';
import Image from 'next/image';

const Index = () => {
  useAuthGuard();

  const placeholders = [
    'Search by User Name',
    'Search by User Id',
    'Search by User No',
  ];

  const [index, setIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex((prevIndex) => (prevIndex + 1) % placeholders.length);
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  return (
    <>
      {/* 🔥 TOP BAR (Matches Screenshot PERFECTLY) */}
      <div className="w-full bg-white px-5 py-3 flex items-center justify-between shadow-sm rounded-md mb-6">

        {/* LEFT — Page Title */}
        <h1 className="text-2xl font-bold text-gray-800">Dashboard</h1>

        {/* CENTER — Search Bar */}
        <div className="flex items-center w-[380px] bg-white border border-gray-300 rounded-full px-4 py-2 shadow-sm">
          <Image src="/search.png" alt="search" width={18} height={18} />
          <input
            type="text"
            placeholder={placeholders[index]}
            className="ml-2 w-full bg-transparent outline-none text-gray-700"
          />
        </div>

        {/* RIGHT — Notification Bell */}
        <div className="relative cursor-pointer">
          <Image src="/notification.png" width={35} height={35} alt="Notification" />
          <span className="absolute top-0 right-0 w-3 h-3 bg-red-500 rounded-full border border-white"></span>
        </div>
      </div>

      {/* MAIN CONTENT */}
      <TopSection />
      <AnalyticsChart />
      <UserTable />
    </>
  );
};

export default Index;
